/2068164_145_b.jpg", "https://i.travelapi.com/hotels/3000000/2070000/2068200/2068164/2068164_128_b.jpg", "https://i.travelapi.com/hotels/3000000/2070000/2068200/2068164/2068164_133_b.jpg", "https://i.travelapi.com/hotels/3000000/2070000/2068200/2068164/2068164_107_b.jpg", "https://i.travelapi.com/hotels/3000000/2070000/2068200/2068164/2068164_125_b.jpg", "https://i.travelapi.com/hotels/3000000/2070000/2068200/2068164/2068164_166_b.jpg", "https://i.travelapi.com/hotels/3000000/2070000/2068200/2068164/2068164_172_b.jpg", "https://i.travelapi.com/hotels/3000000/2070000/2068200/2068164/2068164_135_b.jpg", "https://i.travelapi.com/hotels/3000000/2070000/2068200/2068164/696140cd_b.jpg", "https://i.travelapi.com/hotels/3000000/2070000/2068200/2068164/2068164_104_b.jpg"]
      }]
      for (let x = 0; x < arr.length; x += 1) {
        this.hotelList.list.push(arr[x])
      }
      const obj = {
        'place.latitude': this.inputData.destination.lat,
        'place.longitude': this.inputData.destination.lon,
        checkin: this.inputData.checkin,
        checkout: this.inputData.checkout,
        numberOfAdults: this.inputData.adults,
        numberOfRooms: this.inputData.room,
        numberOfChildren: this.inputData.children.length,
        agesOfChildren: this.inputData.children.join('&')
      }
      this.hotelList.param = JSON.stringify(obj)
      this.hotelList.searchTime = new Date().getTime()
    }, 1000)
  },
  created () {
    const objParam = this.$route.params
    if (objParam.popFlag) {
      const path = this.$route.path
      const arrPath = path.split('/')
      arrPath.pop()
      this.$router.replace(arrPath.join('/'))
    }
  }
}
</script>

<style scoped>
  .listWrapper{
    background-color: #5C6E7C;
    height: 100%;
  }
  .hotelList{
    position: absolute;
    bottom: 48px;
    left: 0;
    top: 70px;
    right: 0;
    overflow: hidden;
    user-select: none;
  }
  .hotelListCont{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #846b55;
    height: 100%;
  }
  .searchPop{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.3);
    z-index: 10;
  }
  .btnContainer{
    position: absolute;
    bottom: 64px;
    width: 240px;
    height: 46px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #1A1A1C;
    border-radius: 23px;
    font-size: 0;
    z-index: 5;
  }
  .btnContainer > button{
    color: #FFF;
    height: 46px;
    padding: 0;
    font-size: 12px;
    padding-left: 12px;
    position: relative;
  }
  .btnContainer > button:nth-child(2) {
    width: 28%;
    position: relative;
  }
  .btnContainer > button:nth-child(1):after{
    border-left: 1px solid #4d4d4d;
    content: '';
    position: absolute;
    right: 0;
    height: 16px;
    top: 15px;
  }
  .btnContainer > button:nth-child(2):after{
    border-right: 1px solid #4d4d4d;
    content: '';
    position: absolute;
    right: 0;
    height: 16px;
    top: 15px;
  }
  .btnContainer > button:first-of-type, .btnContainer > button:last-of-type{
    width: 36%;
  }
  .btnContainer > button:before{
    width: 12px;
    height: 12px;
    background-size: cover;
    position: absolute;
    left: 12px;
    top: 17px;
    content: '';
  }
  .btnContainer > button:first-of-type:before{
    background-image: url(../../assets/filter.svg);
  }
  .btnContainer > button:nth-of-type(2):before{
    background-image: url(../../assets/sorting.svg);
  }
  .btnContainer > button:last-of-type:before{
    background-image: url(../../assets/map.svg);
  }
  .drop-shadow-up {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 48px;
    height: 33px;
    opacity: 0;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), #000000);
    z-index: 1;
    transition: opacity .5s ease;
    pointer-events: none;
  }
  .drop-shadow-up.show{
    opacity: .3;
  }
  .listNoti{
    font-size: 16px; padding: 50px; text-align: center; color: #FFF;
  }
  .pageSelector{
    position: absolute;
    z-index: 5;
    right: 8px;
    top: 8px;
    width: 60px;
    height: 30px;
    opacity: 0;
  }
  .pageSelectorButton{
    position: absolute;
    z-index: 4;
    right: 8px;
    top: 8px;
    width: 60px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    background-color: rgba(216, 216, 216, 0.3);
    border: solid 1px rgba(255, 255, 255, 0.5);
    color: #FFF;
    font-size: 12px;
    border-radius: 4px;
  }
</style>



