<template>
  <div>
    <div class="totalReview">
      <div><span class="roboto">{{ hotelInfo.hotelWeight }}</span><span></span><span>{{ textByWeight }}</span><span v-if="hotelInfo.numberOfReviews">실제 이용 후기 {{ format.numberFormat(hotelInfo.numberOfReviews) }}개 기준</span></div>
      <div class="circleWrapper">
        <percent-circle
          :percent="61"
          :text="'청결도'"
        ></percent-circle>
        <percent-circle
          :percent="48"
          :text="'위치'"
        ></percent-circle>
        <percent-circle
          :percent="91"
          :text="'만족도'"
        ></percent-circle>
        <percent-circle
          :percent="66"
          :text="'부대시설'"
        ></percent-circle>
        <percent-circle
          :percent="80"
          :text="'서비스'"
        ></percent-circle>
      </div>
    </div>
    <div class="reviewList">
      <div>실제 이용 후기</div>
      <ul>
        <li></li>
      </ul>
    </div>
  </div>
</template>

<script>
import PercentCircle from './PercentCircle'
import Format from '../../util/format'
export default {
  components: {
    PercentCircle
  },
  props: ['hotelInfo'],
  computed: {
    textByWeight () {
      let text = ''
      if (this.hotelInfo.hotelWeight > 9) {
        text = '최고'
      } else if (this.hotelInfo.hotelWeight > 8) {
        text = '매우 좋음'
      } else if (this.hotelInfo.hotelWeight > 7) {
        text = '좋음'
      } else if (this.hotelInfo.hotelWeight > 6) {
        text = '만족'
      }
      return text
    }
  },
  data () {
    return {
      format: Format
    }
  }
}
</script>

<style scoped>
  .totalReview{
    padding: 32px 0 0;
  }
  .totalReview > div:first-of-type{
    height: 26px;
    padding: 0 20px;
  }
  .totalReview > div:first-of-type > span{
    float: left;
  }
  .totalReview > div:first-of-type > span:first-of-type{
    font-size: 20px;
    font-weight: bold;
    color: #000;
    line-height: 26px;
  }
  .totalReview > div:first-of-type > span:nth-of-type(2){
    border-right: 1px solid #ddd;
    height: 15px;
    margin: 7px 7px 0 7px;
  }
  .totalReview > div:first-of-type > span:nth-of-type(3){
    font-size: 16px;
    font-weight: bold;
    color: #000;
    line-height: 20px;
    margin-top: 5px;
  }
  .totalReview > div:first-of-type > span:nth-of-type(4){
    font-size: 12px;
    color: #999;
    line-height: 16px;
    margin-top: 7px;
    margin-left: 9px;
  }
  .circleWrapper{
    font-size: 0;
    padding: 20px;
    white-space: nowrap;
    overflow-x: auto;
  }
  .circleWrapper > div{
    margin-right: 33px;
  }
  .reviewList{
    padding: 40px 20px;
    background-color: #f3f2ee;
  }
  .reviewList > div{
    color: #111;
    font-size: 16px;
    font-weight: 600;
  }
  .reviewList > ul{
    margin-top: 28px;
  }
  .reviewList > ul li{
    margin-bottom: 24px;
    background-color: #FFF;
    padding: 20px;
  }
  .reviewList > ul li:last-of-type{
    margin-bottom: 0;
  }
</style>



